<template>
    <div class="quest" style="min-height:85vh;">
        <div id="top"/>
        <div class="sub_menu">
            <el-row>
                <el-col :span="11"><div class="grid-content"></div></el-col>
                <el-col :span="2"><div class="grid-content">
                    <el-button type="info" round v-on:click="logout">logout</el-button>
                </div></el-col>
                <el-col :span="11"><div class="grid-content"></div></el-col>
            </el-row>
        </div>
        <div class="dire-quest" v-if="qActive === true && speedCheck === true">
            <h1>音楽選択システム</h1>
            <div class="block">
                <!-- このblockが、質問１つ分 -->
                <span class="demonstration">
                    Please choose the funniest music you felt. <br><!-- 質問内容(英語) -->
                    あなたが一番聞いていて楽しいと感じた音楽を選んでください<!-- 質問内容(日本語) -->
                </span><br>
                <!---<el-rate v-model="q1" :icon-classes="iconClasses" void-icon-class="el-icon-help" :colors="colors" :max=7></el-rate>ここは全問共通にする -->
            </div>
            <div>
                <el-row>
                    <el-button @click="goToTest1">1</el-button>
                    <el-button type="primary" @click="goToTest2">2</el-button>
                    <el-button type="success" @click="goToTest3">3</el-button>
                    <el-button type="info" @click="goToTest4">4</el-button>
                    <el-button type="warning" @click="goToTest5">5</el-button>
                    <el-button type="danger" @click="goToTest6">6</el-button>
                    </el-row>
            </div><br>

            <div>
                <a href="https://takispig.github.io/db-meter/" target="_blank">このURLを踏んで音楽の音量を60db~70dbに調整して下さい。</a>
            </div><br>

            <div>
                <aplayer :music="music" :list="list" ></aplayer>
            </div>
            <!-- これを押したら、calc関数が発動。計算＋結果画面が表示される -->
            <!--<el-button type="primary" @click="calc" v-scroll-to="'#top'">回答</el-button>-->
        </div>

        <div class="webSpeed" v-if="speedCheck === false">
            <h2>ネットの速度が遅いです！ほかの環境でお試しください！！</h2>
        </div>
    </div>

</template>

<script>
    import firebase from 'firebase'
    import axios from "axios"
    //import vueAplayer from 'vue-aplayer'
    
    //Vue.component('aplayer', vueAplayer)

    export default {
        data() {
            return {
                qActive: true,
                speedCheck: false,
                datasize: 322000,
                ed: 0, tm: 0, sp: 0,
                st: (new Date()).getTime(),
                //質問数に応じて、変数を増やす
                q1: null,
                re: null,//これを計算結果の変数にする
                colors: { 2: '#F7BA2A', 4: { value: '#FF9900', excluded: true }, 5: '#ff6347', 7: '#dc143c' },
                iconClasses: ['el-icon-help', 'el-icon-help', 'el-icon-help'],
                music: {
                    title: 'Lullaby',
                    artist: 'Black and Jones',
                    src: 'https://dotup.org/uploda/dotup.org2920991.mp3',
                    },
                list: [
                {title: 'Lullaby',artist: 'Black and Jones',src: 'https://dotup.org/uploda/dotup.org2920991.mp3',pic: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcToAfsPC5qBsqMS4AzAd8Dfvrlbq-gHA7Kn47_jbk4MvmGZWvhV-VkTo7xA-rp2Iq4aXYk&usqp=CAU"},
                {title:"City of angels",artist:"Black and Jones",src:"https://dotup.org/uploda/dotup.org2921012.mp3",pic: "https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/Eo_circle_blue_number-2.svg/1024px-Eo_circle_blue_number-2.svg.png"},
                {title:"Second Last Chance",artist:"The Overtones",src:"https://dotup.org/uploda/dotup.org2921016.mp3",pic: "https://cdn.pixabay.com/photo/2012/04/23/17/07/three-39116_640.png"},
                {title:"End In The Ocean/Oh Life",artist:"Avalanche City",src:"https://dotup.org/uploda/dotup.org2921018.mp3",pic: "https://upload.wikimedia.org/wikipedia/commons/thumb/e/ec/NYCS-bull-trans-4.svg/2048px-NYCS-bull-trans-4.svg.png"},
                {title:"Il Brutto",artist:"Crookers",src:"https://dotup.org/uploda/dotup.org2921023.mp3",pic: "https://upload.wikimedia.org/wikipedia/commons/thumb/7/78/NYCS-bull-trans-5.svg/2048px-NYCS-bull-trans-5.svg.png"},
                {title:"Il Buono",artist:"Crookers",src:"https://dotup.org/uploda/dotup.org2921027.mp3",pic: "https://upload.wikimedia.org/wikipedia/commons/thumb/5/53/NYCS-bull-trans-6.svg/2048px-NYCS-bull-trans-6.svg.png"}
                ]
            }
        },
        methods: {
            //ボタン操作など、外部から呼び出して動作する関数を入れておく。
            calc(){ //ここで、ポチポチ押してもらった内容で計算をする
                this.re = this.q1 //ここで計算！！！！！！
                this.qActive =false //アンケートの入力完了ボタンが押されたら->結果表示に切り替わる
            },
            goToTest1(){ //「認知症検査に進む」ボタンを押したら、認知症検査用のページに飛ぶ
                this.$router.push('/about').catch(()=>{}); //希望のページに飛ばすためのコード
            },
            goToTest2(){ //「認知症検査に進む」ボタンを押したら、認知症検査用のページに飛ぶ
                this.$router.push('/about2').catch(()=>{}); //希望のページに飛ばすためのコード
            },
            goToTest3(){ //「認知症検査に進む」ボタンを押したら、認知症検査用のページに飛ぶ
                this.$router.push('/about3').catch(()=>{}); //希望のページに飛ばすためのコード
            },
            goToTest4(){ //「認知症検査に進む」ボタンを押したら、認知症検査用のページに飛ぶ
                this.$router.push('/about4').catch(()=>{}); //希望のページに飛ばすためのコード
            },
            goToTest5(){ //「認知症検査に進む」ボタンを押したら、認知症検査用のページに飛ぶ
                this.$router.push('/about5').catch(()=>{}); //希望のページに飛ばすためのコード
            },
            goToTest6(){ //「認知症検査に進む」ボタンを押したら、認知症検査用のページに飛ぶ
                this.$router.push('/about6').catch(()=>{}); //希望のページに飛ばすためのコード
            },
            logout: function(){ //一定時間操作されていなかったら、強制ログアウトする
                clearTimeout(this.timeoutTimer)
                this.logouotRequest(this.email)
                firebase.auth().signOut();
                clearTimeout(this.timeoutTimer)
                this.$router.push('/')
            },
            logouotRequest: async function(username){ //firebaseに、ログアウトの支持を飛ばす
                const params = new URLSearchParams();
                params.append("name",username);
                await axios.post("http://localhost:8040/logout", params)
                //await axios.post("https://asia-east1-twitch-MCI.cloudfunctions.net/logout", params)
                alert("ログアウトしました")
            },
        },
        mounted: function(){ 
            //mountedの中には、methodsよりも先に処理してほしい内容を書く
            //ログインした後に、ユーザー（プレイヤー）のネット回線速度を計算。遅かったらアラートを出して、強制ログアウトさせる。
            this.ed = (new Date()).getTime()
            this.tm = (this.ed - this.st) / 1000
            this.sp = Math.floor(this.datasize /this. tm)
            if(this.sp > 5000000){
                this.speedCheck = true
            }else{
                alert("インターネット速度がかなり遅いです。\nほかの環境で再度お試しください")
                this.logout()
            }
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<!-- webページの表示関連のデザイン。文字サイズや色の指定 -->
<style scoped>
h1, h2 {
  font-weight: normal;
}

.block{
    margin-top: 10px;
}

.el-button{
    margin-top: 20px;
}

.sub_menu{
  margin: 0em;
}
</style>
